	$bg-color: #242324;
	// $cell-shadow: inset 0 0 3px 0 #000, 0 -2px 2px -1px #000, -2px 0 2px -2px #28221f, -2px 0 2px -2px #28221f, 2px 0 2px -2px #28221f, 2px 0 2px -2px #28221f, 0 3px 3px -3px #8f8c8b, 0 2px 2px -2px #8f8c8b, 0 1px 1px -1px #8f8c8b;
	$cell-shadow: inset 8px 0 4px -8px #000, inset -8px 0 4px -8px #000, inset 0 8px 2px -8px #e3e3e3, inset 0 10px 2px -8px #282828, inset 0 -9px 2px -8px #000;
	border-spacing: 0px;
	border-collapse: collapse;
	width: 100%;
	&.sticky-header {
		thead {
			position: sticky;
			top: 0;
			z-index: 10;
		}
	}
	thead {
		th {
			background: $bg-color;
			font-weight: 600;
			padding: 10px;
			box-shadow: $cell-shadow;
			&.sortable {
				cursor: pointer;
				i:before {
					padding-right: 5px;
				}
				&:hover,
				&:focus,
				&.hover,
				&.focus {
					i:before {
						color: #ffe6bf;
					}
				}
				&:active,
				&.active {
					i:before {
						color: #f0bd64;
					}
				}
			}
		}
	}
	tbody {
		td {
			background: $bg-color;
			padding: 8px;
			box-shadow: $cell-shadow;
		}
	}
}
