* {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	box-sizing: border-box;
}

html {
	height: 100%;
}

body {
	background-color: #201810;
	background: #201810;
	background-image: url("../img/default_bg.jpg"), url(data:image/jpeg;base64,);
	background-size: 2048px 3072px;
	background-position: center top;
	line-height: 1;
	color: #fff;
	font-family: titillium web, sans-serif;
	font-size: 100%;
	height: 100%;
}

#root {
	height: 100vh;
	display: flex;
}

.flex-column-fill {
	display: flex;
	flex-direction: column;
	flex: 1;
	overflow: hidden;
}

// TODO: move to separate element?
h1,
h2,
h3,
h4,
h5 {
	color: #ffe6c0;
	font-weight: 700;
	line-height: 1.25
}

h1 {
	font-size: 130%
}

h2 {
	font-size: 120%
}

h1,
h2 {
	font-weight: 900;
	margin-bottom: 12px;
	cursor: default
}

h3 {
	font-size: 116%;
	font-weight: 900;
}

h4 {
	font-size: 107%;
	font-weight: 400;
}
